<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="papers" style="width: 100%">
          <el-table-column label="问卷ID" prop="paperID" v-if="show = false"></el-table-column>
          <el-table-column label="问卷名称" prop="paperName"></el-table-column>
          <el-table-column label="创建时间" prop="createTime"></el-table-column>
          <el-table-column align="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="success"
                @click="test(scope.row)"
                icon="el-icon-edit"
                circle
              />
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
      total: 0,
      papers: []
    };
  },
  methods: {
    test(row) {
      this.$router.push({name: 'cPaperDetail', params: { paperID: row.paperID}})
      console.log("进入的试卷ID为：" + row.paperID)
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/paper/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.papers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/paper/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.papers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

